.App {
  display: grid;
  grid-area:auto;
  grid-template-columns: 1fr;
  background-color: rgb(245, 245, 245);
  text-align: center;
  height: auto;
  justify-content: center;
  
}



section{
  background-color: rgb(173, 171, 171);
  width: 90%;
  text-align: center;
  margin: 0 auto;
display: flex;
flex-direction: column;
margin-top: 5px;
margin-bottom: 5px;
padding-top: 10px;
}


.repositorios{
  background-color: rgb(252, 243, 251);
  width: 90%;
 padding-left: 10px;
  text-align: left;
 margin-bottom: 20px;
height: auto;
border-radius: 20px;

}
a{
text-decoration: none;
color: blue;
}

a:hover{
  color: rgb(201, 57, 57);
}

@media screen and (max-width:480px) { 
  .App{
   width: 100%;
 
  }

section{
  width: auto;
}


}


@media only screen and (max-width:719px){
  section {
  
    width: 100%;
  
  }
  
}


@media only screen and (min-width:720px){
  section {
    width: 100%;
 
  }

  .App{
    width: 100%;
    
  }



}


